* {
  box-sizing: border-box;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 1040px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.loader-form {
  width: 10px;
  height: 10px;
  background-color: #ff4500;
  border-radius: 50%;
  animation: flash .5s ease-out infinite alternate;
  position: absolute;
  top: 50%;
  left: 40%;
  box-shadow: 32px 0 #ff4500, -32px 0 #ff4500;
}

@keyframes flash {
  0% {
    background-color: rgba(255, 255, 255, .133);
    box-shadow: 32px 0 rgba(255, 255, 255, .133), -32px 0 #ff4500;
  }

  50% {
    background-color: #ff4500;
    box-shadow: 32px 0 rgba(255, 255, 255, .133), -32px 0 rgba(255, 255, 255, .133);
  }

  100% {
    background-color: rgba(255, 255, 255, .133);
    box-shadow: 32px 0 #ff4500, -32px 0 rgba(255, 255, 255, .133);
  }
}

.loader {
  color: #ff4500;
  text-indent: -9999em;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  font-size: 45px;
  animation: mltShdSpin 1.7s infinite, round 1.7s infinite;
  position: relative;
  overflow: hidden;
  transform: translateZ(0);
}

@keyframes mltShdSpin {
  0% {
    box-shadow: 0 -.83em 0 -.4em, 0 -.83em 0 -.42em, 0 -.83em 0 -.44em, 0 -.83em 0 -.46em, 0 -.83em 0 -.477em;
  }

  5%, 95% {
    box-shadow: 0 -.83em 0 -.4em, 0 -.83em 0 -.42em, 0 -.83em 0 -.44em, 0 -.83em 0 -.46em, 0 -.83em 0 -.477em;
  }

  10%, 59% {
    box-shadow: 0 -.83em 0 -.4em, -.087em -.825em 0 -.42em, -.173em -.812em 0 -.44em, -.256em -.789em 0 -.46em, -.297em -.775em 0 -.477em;
  }

  20% {
    box-shadow: 0 -.83em 0 -.4em, -.338em -.758em 0 -.42em, -.555em -.617em 0 -.44em, -.671em -.488em 0 -.46em, -.749em -.34em 0 -.477em;
  }

  38% {
    box-shadow: 0 -.83em 0 -.4em, -.377em -.74em 0 -.42em, -.645em -.522em 0 -.44em, -.775em -.297em 0 -.46em, -.82em -.09em 0 -.477em;
  }

  100% {
    box-shadow: 0 -.83em 0 -.4em, 0 -.83em 0 -.42em, 0 -.83em 0 -.44em, 0 -.83em 0 -.46em, 0 -.83em 0 -.477em;
  }
}

@keyframes round {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.apiform {
  width: 100%;
  max-width: 286px;
  min-height: 72px;
  background-color: #fcfcfc;
  border-radius: 4px;
  padding: 24px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.cat-info {
  display: flex;
}

.cat-foto {
  max-width: 450px;
  max-height: 300px;
  margin-right: 10px;
  display: block;
}

.cat-name {
  margin-top: 10px;
  margin-bottom: 15px;
}

.cat-description {
  max-width: 500px;
}

.tempword-wrap {
  font-weight: 600;
}

